import React, { Component } from 'react';
import './App.css';
import { List, ListItem } from 'material-ui/List';
import aboutPic from './assets/images/about-pic.jpg';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import { Toolbar, ToolbarGroup, ToolbarTitle, MenuItem, Avatar, Table, TableRow, TableBody, TableRowColumn, CardTitle, CardText, Chip } from 'material-ui';
import Projects from './assets/Projects';
import Skills from './assets/Skills';

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      selectedIndex: 0,
    }
  }

  render() {
    return (
      <MuiThemeProvider>
        <div className="App">

          <Toolbar style={{ backgroundColor: 'transparent', }}>
            <ToolbarGroup firstChild={true}>
              <ToolbarTitle text="HemantKaushik" style={{ paddingLeft: 16, paddingRight: 16, }} />
            </ToolbarGroup>
            <ToolbarGroup lastChild={true}>
              <MenuItem
                primaryText="Home"
                disabled={this.state.selectedIndex === 0}
                onClick={() => this.select(0)} />
              <MenuItem
                primaryText="Work"
                disabled={this.state.selectedIndex === 1}
                onClick={() => this.select(1)} />
              <MenuItem
                primaryText="Contact"
                disabled={this.state.selectedIndex === 2}
                onClick={() => this.select(2)} />
            </ToolbarGroup>
          </Toolbar>

          <div className="App-contents">
            {this.state.selectedIndex === 0 && <About />}
            {this.state.selectedIndex === 1 && <Work />}
            {this.state.selectedIndex === 2 && <Contact />}
          </div>

        </div>
      </MuiThemeProvider>
    );
  }

  select = (index) => {
    this.setState({ selectedIndex: index });
  }
}

class About extends Component {


  styles = {

    chip: {
      margin: 4,
    },
    chipListWrapper: {
      display: 'flex',
      flexWrap: 'wrap',
      padding: 16,
      justifyContent: 'center',
    },
      box: {
        padding: 16,
      },
      rotateImage: {
        transform: 'rotate(90deg)',
      }
    };

  render() {
    return (
      <div className="About">

        <Avatar
          src={aboutPic}
          size={256}
          className="About-pic" />

        <h1 style={this.styles.box} className="About-title">Hemant Kaushik</h1>

        <p style={this.styles.box} className="About-desc">
          A passionate developer who always ready to learn new things.
          <br />
          4+ years experience in mobile and web technologies.
        </p>

        <div style={this.styles.chipListWrapper}>
          {
            Object.keys(Skills).map(tag => {
              let skill = Skills[tag];
              return (
                <Chip
                  key={skill.name}
                  title={skill.description}
                  onClick={() => { window.open(skill.link, '_blank') }}
                  style={this.styles.chip}>
                  {skill.name}
                </Chip>
              );
            })}
        </div>

      </div>
    );
  }
}

class Work extends Component {

  styles = {
      root: {
        textAlign: 'initial',
        width: '100%',
      },
      listItemWrapper: {
        backgroundColor: '#fafafa',
        display: 'flex',
        flexDirection: 'row',
      },
      chip: {
        margin: 4,
      },
      chipListWrapper: {
        display: 'flex',
        flexWrap: 'wrap',
        padding: 16,
      },
    };

  getAvatar = (icon) => {
    if (icon != null) {
      return <Avatar
        src={icon}
        style={{ margin: 24, }}
        size={96} />
    } else {
      return <div style={{ margin: 24, width: 96, height: 96, }} />
    }
  }

  render() {
    let projects = Projects;

    return (
      <div style={this.styles.root}>

        <List>
          {projects.map((project) => (

            <ListItem
              key={project.title}
              onClick={() => { window.open(project.link, '_blank') }}>

              <div style={this.styles.listItemWrapper} >

                {this.getAvatar(project.icon)}

                <div>

                  <CardTitle
                    title={project.title}
                    subtitle={project.subtitle} />

                  <CardText>{project.desc}</CardText>

                  {/* <div style={this.styles.chipListWrapper}>
                    {project.tags.map(tag => (
                      <Chip key={tag.name} style={this.styles.chip}>{tag.name}</Chip>
                    ))}
                  </div> */}

                </div>

              </div>
            </ListItem>
          ))}
        </List>

      </div>
    );
  }
}

class Contact extends Component {

  constructor(props) {
    super(props);

    this.state = {
      values: [{
        title: "Twitter",
        id: 'about_hemant',
        link: "https://www.twitter.com/about_hemant",
      }, {
        title: "StackOverflow",
        id: 'hemant-kaushik',
        link: "https://stackoverflow.com/users/7784230/hemant-kaushik",
      }, {
        title: "Skype",
        id: 'live:thehemantkaushik',
        link: "live:thehemantkaushik",
      }, {
        title: "Email",
        id: 'hi@thehemantkaushik.com',
        link: "mailto:hi@thehemantkaushik.com",
      }, {
        title: "Mobile",
        id: '+919017794910',
        link: "tel:+919017794910",
      },
      ]
    };
  }

  render() {
    return (
      <div className="Contact">
        <Table
          selectable={false}
          multiSelectable={false}>
          <TableBody displayRowCheckbox={false}>
            {this.state.values.map(item => (
              <TableRow key={item.title}>
                <TableRowColumn>{item.title}</TableRowColumn>
                <TableRowColumn><a href={item.link}>{item.id}</a></TableRowColumn>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>
    );
  }
}

export default App;



